<script>
export let title;
export let body;

let toggle = false;

const handleClick = function(event) {
  this.classList.toggle("active");
  let body = this.nextElementSibling;
  if (body.style.maxHeight) {
    body.style.maxHeight = null;
  } else {
    body.style.maxHeight = body.scrollHeight + "px";
  }
}
</script>

<style lang="scss">@charset "UTF-8";
#shade {
  border: 1px solid black;
  overflow: hidden;
}
#shade #title {
  grid-area: title;
  cursor: pointer;
  min-width: 300px;
  padding: 18px 18px;
  text-align: left;
  background-color: #F2F3F9;
  color: #2E2E2E;
}
#shade #title:after {
  content: "➕";
  float: right;
}
#shade button {
  grid-area: toggle;
}
#shade #body {
  grid-area: body;
  padding: 0 18px;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
  background-color: #f1f1f1;
  text-align: left;
}
/*# sourceMappingURL=components/Shade.svelte.map */</style>

<div id="shade">
  <button on:click={handleClick} id="title">{title}</button>
  <div id="body">
    {body}
  </div>
</div>
